.videoList_wrapper h3 {
    text-align: center;
    margin: 20px;
    color: #353535;
    font-weight: 300;
    font-size: 21px;
    display: block;
    text-decoration: none;
}

.videoListItem_wrapper {
    display: flex;
    border-bottom: 2px solid #d5d5d5;
    background: #ffffff;
}

.videoListItem_wrapper h2 {
    font-size: 13px;
    color : #525252;
}

.left { 
    background-size: cover !important;
}

.left>div {
    width: 90px;
    height: 90px;
    background-size: 40px !important;
    background-repeat: no-repeat !important;
    background-position: center center !important;
    background: url("./play/play.png");
}

.right {
    flex-grow: 1;
    padding: 10px;
}

.relatedWrapper {
    margin: 0 5px;
}